
import { defineComponent } from 'vue'

export default defineComponent({
  name: "versioninformation",
  props: {
    version: String,
    commitTimestamp: {
      type: Number,
      default: 0
    }
  },
  computed: {
    date(): string {
      return new Date(this.commitTimestamp).toUTCString()
    }
  }
})
